.main {
  display: flex;
  max-width: 1200px;
  grid-gap: 30px;
  gap: 30px;
}
.main .report {
  display: flex;
  flex-direction: column;
  width: 250px;
  background-color: #1c1f4a;
  border-radius: 15px;
}
.main .report .header {
  width: 250px;
  height: 70%;
  background-color: #5847eb;
  border-radius: 15px;
  padding: 35px;
}
.main .report .header .profil-img {
  border-radius: 40px;
  border: 3px solid #fff;
  width: 80px;
  height: 80px;
}
.main .report .header .profil-name {
  margin-top: 50px;
}
.main .report .content {
  height: 30%;
}
.main .report .content ul {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 0 35px 10px 35px;
  height: 100%;
}
.main .report .content ul li {
  cursor: pointer;
}
.main .boxes {
  display: flex;
  flex-direction: row;
  width: 950px;
  grid-gap: 30px;
  gap: 30px;
  flex-wrap: wrap;
}
.main .boxes > div {
  border-radius: 15px 15px 50px 50px;
}
.main .boxes .header {
  height: 45px;
  width: 250px;
}
